  border-radius: 3px;
  transition: all 200ms ease;
  cursor: pointer;
}
.branch-grid-item label input[type=checkbox] + i::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 17px;
  background-image: url(../img/icon-sprite.svg#checked);
  background-repeat: no-repeat;
